<template>
	<div id="mobileIndex">
		<div class="mobilebgcolor">
		</div>
		<div class="imgrotate" @click="$router.push('homeList')">
			<div class="headimg"></div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'mobileIndex',
		data() {
			return {

			}
		},
		created() {
		}
	}
</script>

<style lang="scss">
	#mobileIndex {
		height: 100%;
		width: 100%;    
		position: relative;
	}

	.mobilebgcolor {
		height: 100%;
		width: 100%;
		position: absolute;
		background-image: url(../../assets/imgs/new.jpg);
		background-size: 100%;
		background-repeat: no-repeat;
		background-position: 32%;
		background-size: cover;
		filter: blur(15px);
	}

	.imgrotate {
		width: 2rem;
		height: 2rem;
		border-radius: 50%;
		position: absolute;
		left: 50%;
		top: 50%;
		border: 1px solid #d1c2ff;
		box-shadow: 0px 0px 6px #d1c2ff;
		transform: translate(-50%, -50%);
	}

	.imgrotate>.headimg {
		width: 100%;
		height: 100%;
		border-radius: 50%;
		background-image: url(../../assets/imgs/new.jpg);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 18%;
		animation: rotatediv 20s linear infinite;
	}

	#audiomp3 {
		position: absolute;
		left: 0;
		top: 0;
		height: inherit;
		width: inherit;
	}

	@keyframes rotatediv {
		from {
			transform: rotate(0deg);
		}

		to {
			transform: rotate(720deg);
		}
	}
</style>



