<template>
  <div id="homeList">
    <div class="carousel">
      <el-carousel trigger="click" height="150px" indicator-position="none">
        <el-carousel-item v-for="(item,index) in storyData" :key="index">
          <!-- <h3 class="small">{{ 123456 }}</h3> -->
          <div style="height: 100%;width: 100%;">
            <!-- <img :src="item.url" alt="img" style="height: 100%;width: 100%;"> -->
            <img v-show="item.importUrl" :src="item.importUrl" alt="img" style="height: 100%;width: 100%;border-radius: 0.1rem;">
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="fine margin2">
      <div class="fineText" v-for="(item,index) in fineTextArr" :key="index">
        {{item.name}}
      </div>
    </div>
    <div class="timerDiv margin2">
      <timer></timer>
    </div>
    <div class="storyTittle">
      <span>Stories</span>
      <!-- <span>更多<i class="el-icon-arrow-right"></i></span> -->
    </div>
    <div class="storyList margin2">
      <div class="stotyOutdiv" v-for="(item,index) in storyData" :key="index" @click="goListDetail(item,index)">
        <div class="storydiv">
          <img :src="item.importUrl" alt="img" style="height: 100%;width: 100%;border-radius: 0.1rem;">
        </div>
        <div class="storydesc">
          {{item.decs}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import timer from '../../commonComponent/timer.vue';
  import cont from '../storyListDetail/storyListDetail.vue';
  import {
    mapState
  } from 'vuex';
  export default {
    data() {
      return {
        fineTextArr: [{
            name: '美'
          },
          {
            name: '好'
          },
          {
            name: '时'
          },
          {
            name: '光'
          },
          {
            name: '海'
          },
          {
            name: '苔'
          },
        ],
        storyData: [],
        imgList: [
          {
            url:require("../../../assets/imgs/new.jpg")
          },
          {
            url:require("../../../assets/imgs/newdog.jpg")
          }
        ]
      }
    },
    components: {
      timer,
      cont
    },
    computed: { //这里的三点叫做 : 扩展运算符
      ...mapState({
        cont: state => state.content.cont
      })
    },
    methods: {
      goListDetail(item,i) {
        this.$router.push({
          name:'storyListDetail',
          query:{
            // id:item.id
            id:i
          }
        })
      }
    },
    created() {
      this.storyData = this.cont;
    }
  }
</script>

<style scoped="scoped">
  #homeList {
    height: inherit;
    padding: 0.3rem;
    color: #0d1e7f;
  }

  .carousel {
    height: 24vh;
  }

  /* .timerDiv{
    margin: 0.2rem 0;
  } */
  .margin2 {
    margin: 0.2rem 0;
  }

  .fine {
    /* background-color: #006666; */
    box-sizing: border-box;
    padding: 0.08rem;
    /* height: 1.5rem; */
    width: 100%;
    display: flex;
    /* justify-content: space-around; */
    align-items: center;
    overflow-x: auto;
  }

  .fineText {
    width: 1.3rem;
    height: 1.3rem;
    line-height: 1.3rem;
    text-align: center;
    border-radius: 50%;
    border: 0.03rem solid #cb9dff;
    flex: 1 0 auto;
    margin-left: 0.3rem;
    color: #cb9dff;
    font-size: 0.6rem;
  }

  .storyTittle {
    display: flex;
    justify-content: space-between;
    margin: 0.6rem 0 0.2rem 0;
  }

  .storyList {
    display: flex;
    flex-wrap: wrap;
  }

  .stotyOutdiv {
    width: 2rem;
    margin-left: 0.22rem;
    margin-bottom: 0.22rem;
  }

  .storydiv {
    box-sizing: border-box;
    /* padding: 0.1rem; */
    background: #f1e0ff;
    width: inherit;
    height: 2rem;
    border-radius: 0.12rem;
  }

  .storydesc {
    margin-top: 0.22rem;
    margin-bottom: 0.11rem;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .el-carousel__item {
    border-radius: 0.1rem;
  }
</style>
<style>
  .carousel>.el-carousel>.el-carousel__container {
    height: 24vh !important;
  }
  @media screen and (max-width:1000px){
    .el-carousel__arrow{
      display: none;
    }
  }
</style>



