<template>
  <div id="storyListDetail">
    <div class="storyListDetail-title">
      <img :src="nodeData.titleImg" alt="123" style="width: 100%;height: 100%;border-radius: 0.1rem;">
      <div class="storyListDetail-mc">
        <div class="storyListDetail-mc-div">
          <div style="margin-top:0.3rem;font-size:0.34rem">
            {{nodeData.title}}
          </div>
          <div style="margin-top:0.3rem;font-size:0.30rem">
            {{nodeData.startTime}}&nbsp;至&nbsp;{{nodeData.endTime}}
          </div>
        </div>
        <span style="position: absolute;left: 6%;bottom: 4%;color: #fff;font-size:0.20rem">
          <i class="el-icon-user" style="font-size:0.28rem"></i>
          <span>{{nodeData.num}}</span>人
        </span>
        <span style="position: absolute;right: 6%;bottom: 4%;color: #fff;font-size:0.20rem">
          <i class="el-icon-time" style="font-size:0.28rem"></i>
          <span>1</span>天&nbsp;记录时间：<span>{{nodeData.createTime}}</span>
        </span>
      </div>
    </div>
    <div style="text-indent: 0.4rem;text-align: left;margin-top: 0.4rem;line-height: 0.34rem;letter-spacing: 0.03rem;font-size: 0.28rem;">
      <span style="font-size: 0.4rem;">{{nodeData.text.substr(0,2)}}</span>{{nodeData.text.substr(2,nodeData.text.length)}}
    </div>
    <div style="text-align: center;color: #999999;padding-top: 1rem;font-size: 0.1rem;">
     ----------{{nodeData.author}}----------
    </div>
  </div>
</template>

<script>
  import {
    mapState
  } from 'vuex';
  export default {
    name: 'storyListDetail',
    data() {
      return {
        nodeData: {}
      }
    },
    created() {
      this.getInit();
    },
    methods: {
      getInit() {
        let id = this.$route.query.id;
        let cont = this.cont;
        this.nodeData = this.cont[id];
        // for (let i in cont) {
        //   if (cont[i].id == id) {
        //     this.nodeData = cont[i]
        //   }
        // }
      }
    },
    computed: {
      ...mapState({
        cont: state => state.content.cont
      })
    }
  }
</script>

<style scoped="scoped" lang="scss">
  $radiusDiv:0.1rem;

  #storyListDetail {
    text-align: center;
    padding: 0.3rem;
  }

  .storyListDetail-title {
    height: 21.5vh;
    position: relative;
    width: inherit;
    border-radius: $radiusDiv;
  }

  .storyListDetail-mc {
    width: 100%;
    height: inherit;
    border-radius: $radiusDiv;
    position: absolute;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.4);
  }

  .storyListDetail-mc-div {
    border: 0.01rem solid #fff;
    border-left: none;
    border-right: none;
    width: 60%;
    height: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
  }
</style>



