<template>
  <div id="timer">
    <div>

      <div style="display: flex;align-items: center;justify-content: center;">
        <div v-show="currentTime.yearFour>0" style="display: inline-block;">
          <div class="wheel" v-show="currentTime.yearOne>0" :class="getBefore(currentTime.yearOne)">

          </div>
          <div class="wheel" v-show="currentTime.yearTwo>0" :class="getBefore(currentTime.yearTwo)">

          </div>
          <div class="wheel" v-show="currentTime.yearThree>0" :class="getBefore(currentTime.yearThree)">

          </div>
          <div class="wheel" v-show="currentTime.yearFour>0" :class="getBefore(currentTime.yearFour)">

          </div>
          <span>&nbsp;年</span>
        </div>
        <div class="wheel" :class="getBefore(currentTime.mouthOne)">

        </div>
        <div class="wheel" :class="getBefore(currentTime.mouthTwo)">

        </div>
        <span>&nbsp;月</span>
        <div class="wheel" :class="getBefore(currentTime.dateOne)">

        </div>
        <div class="wheel" :class="getBefore(currentTime.dateTwo)">

        </div>
        <span>&nbsp;日</span>
        <div class="wheel" :class="getBefore(currentTime.hourOne)">

        </div>
        <div class="wheel" :class="getBefore(currentTime.hourTwo)">

        </div>
        <span>&nbsp;时</span>
        <div class="wheel" :class="getBefore(currentTime.minuteOne)">

        </div>
        <div class="wheel" :class="getBefore(currentTime.minuteTwo)">

        </div>
        <span>&nbsp;分</span>
        <div class="wheel" :class="getBefore(currentTime.secondOne)">

        </div>
        <div class="wheel" :class="getBefore(currentTime.secondTwo)">

        </div>
        <span>&nbsp;秒 </span>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        currentTime: {},
      }
    },
    mounted() {
      setInterval(() => {
        let dateObj = this.diffDate('2020-02-20 13:54:00', this.getNowFormatDate())
        this.currentTime = dateObj;
      }, 1000)
    },
    methods: {
      diffDate(date1, date2) {
        let begin = new Date(date1);
        let end = new Date(date2);

        // 如果开始时间晚于结束时间，交换日期，并记录交换状态
        let swap = false;
        if (begin > end) {
          let tmp = begin;
          begin = end;
          end = tmp;
          swap = true;
        }

        // 分别取二个日期的年、月、日值
        let d1 = begin.getDate();
        let m1 = begin.getMonth() + 1;
        let y1 = begin.getFullYear();
        let d2 = end.getDate();
        let m2 = end.getMonth() + 1;
        let y2 = end.getFullYear();

        // 获取每个月的天数，这里要注意一下闰年的2月
        var getMonthDays = function(y, m) {
          var aMonthDays = [0, 31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
          if (m == 2 && ((y % 400 == 0) || (y % 4 == 0 && y % 100 != 0))) {
            return 29;
          }
          return aMonthDays[m];
        };

        let y, m, d
        let tempD = 0
        let tmpM = 0
        let tmpY = 0

        // 计算日，不足时向月份借
        if (d2 >= d1) {
          d = d2 - d1
        } else {
          tmpM = -1
          d = getMonthDays(y1, m1) + d2 - d1
        }

        // 计算月，不足时向年份借
        if (m2 + tmpM >= m1) {
          m = m2 + tmpM - m1
        } else {
          tmpY = -1
          m = 12 + m2 + tmpM - m1
        }

        // 计算年
        y = y2 + tmpY - y1

        // 拼接距离字符串 输出格式如："5天"， "5月 05天"， "5年 05月 05日"
        let str = "";
        if (y > 0) {
          str = y + '年 ' + ("0" + m).substr(-2) + "月 " + ("0" + d).substr(-2) + '天 '
        } else if (m > 0) {
          str = m + "月 " + ("0" + d).substr(-2) + '天 '
        } else if (d > 0) {
          str = d + '天 '
        }
        let timeMiao = new Date(date1).getTime()
        let thatTimeMiao = new Date(date2).getTime()
        let chaTime = thatTimeMiao - timeMiao;
        let day = parseInt(chaTime / 86400000)
        let yu = chaTime % 86400000
        let hour = parseInt(yu / 3600000)
        let yuH = yu % 3600000
        let min = parseInt(yuH / 60000)
        let yuM = yuH % 60000
        let sec = parseInt(yuM / 1000)
        // 完整输出
        // return date1 + " 比 " + date2 + (swap ? " 晚 " : " 早 ") + str;
        // return str + hour + '时 ' + min + '分 ' + sec + '秒'//早前return
        // ==============================
        // 年
        let yearOne = 0;
        let yearTwo = 0;
        let yearThree = 0;
        let yearFour = 0;
        y = y.toString();
        if (y.length == 4) {
          yearOne = y.substr(0, 1);
          yearTwo = y.substr(1, 2);
          yearThree = y.substr(2, 3);
          yearFour = y.substr(3, 4);
        } else if (y.length == 3) {
          // yearOne = 0;
          yearTwo = y.substr(0, 1);
          yearThree = y.substr(1, 2);
          yearFour = y.substr(2, 3);
        } else if (y.length == 2) {
          // yearOne = 0;
          // yearTwo = 0;
          yearThree = y.substr(0, 1);
          yearFour = y.substr(1, 2);
        } else if (y.length == 1) {
          // yearOne = 0;
          // yearTwo = 0;
          // yearThree = 0;
          yearFour = y.substr(0, 1);
        }
        // ===============
        // 月
        let mouthOne = 0;
        let mouthTwo = 0;
        m = m.toString();
        if (m.length == 2) {
          mouthOne = m.substr(0, 1);
          mouthTwo = m.substr(1, 2);
        } else if (m.length == 1) {
          // mouthOne = m.substr(0, 1);
          mouthTwo = m.substr(0, 1);
        }
        // 日
        let dateOne = 0;
        let dateTwo = 0;
        d = d.toString();
        if (d.length == 2) {
          dateOne = d.substr(0, 1);
          dateTwo = d.substr(1, 2);
        } else if (d.length == 1) {
          // dateOne = d.substr(0, 1);
          dateTwo = d.substr(0, 1);
        }
        // 时
        let hourOne = 0;
        let hourTwo = 0;
        hour = hour.toString();
        if (hour.length == 2) {
          hourOne = hour.substr(0, 1);
          hourTwo = hour.substr(1, 2);
        } else if (hour.length == 1) {
          // hourOne = hour.substr(0, 1);
          hourTwo = hour.substr(0, 1);
        }
        // 分
        let minuteOne = 0;
        let minuteTwo = 0;
        min = min.toString();
        if (min.length == 2) {
          minuteOne = min.substr(0, 1);
          minuteTwo = min.substr(1, 2);
        } else if (min.length == 1) {
          // minuteOne = min.substr(0, 1);
          minuteTwo = min.substr(0, 1);
        }
        // 秒
        let secondOne = 0;
        let secondTwo = 0;
        sec = sec.toString();
        if (sec.length == 2) {
          secondOne = sec.substr(0, 1);
          secondTwo = sec.substr(1, 2);
        } else if (sec.length == 1) {
          // secondOne = sec.substr(0, 1);
          secondTwo = sec.substr(0, 1);
        }
        // ===============
        let newobj = {
          yearOne: parseInt(yearOne),
          yearTwo: parseInt(yearTwo),
          yearThree: parseInt(yearThree),
          yearFour: parseInt(yearFour),
          mouthOne: parseInt(mouthOne),
          mouthTwo: parseInt(mouthTwo),
          dateOne: parseInt(dateOne),
          dateTwo: parseInt(dateTwo),
          hourOne: parseInt(hourOne),
          hourTwo: parseInt(hourTwo),
          minuteOne: parseInt(minuteOne),
          minuteTwo: parseInt(minuteTwo),
          secondOne: parseInt(secondOne),
          secondTwo: parseInt(secondTwo)
        }

        // console.log(newobj, 'dksfalkdjlkfjlsdkj', str + hour + '时 ' + min + '分 ' + sec + '秒')
        return newobj;
      },
      getNowFormatDate() {
        var date = new Date();
        var seperator1 = "-"; //设置成自己想要的日期格式 年/月/日
        var seperator2 = ":"; //设置成自己想要的时间格式 时:分:秒
        var month = date.getMonth() + 1; //月
        var strDate = date.getDate(); //日
        var Hours = date.getHours(); //时
        var Minutes = date.getMinutes(); //分
        var Seconds = date.getSeconds(); //秒
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        if (Hours >= 0 && Hours <= 9) {
          Hours = "0" + Hours;
        }
        if (Minutes >= 0 && Minutes <= 9) {
          Minutes = "0" + Minutes;
        }
        if (Seconds >= 0 && Seconds <= 9) {
          Seconds = "0" + Seconds;
        }
        var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate +
          " " + Hours + seperator2 + Minutes +
          seperator2 + Seconds;
        return currentdate;
        let mouthOne = month.substr(0, 1);
        let mouthTwo = month.substr(1, 2);
        let dateOne = strDate.substr(0, 1);
        let dateTwo = strDate.substr(1, 2);
        let hourOne = Hours.substr(0, 1);
        let hourTwo = Hours.substr(1, 2);
        let minuteOne = Minutes.substr(0, 1);
        let minuteTwo = Minutes.substr(1, 2);
        let secondOne = Seconds.substr(0, 1);
        let secondTwo = Seconds.substr(1, 2);
        // let newobj={
        // 	yearOne:date.getFullYear().substr(0,1);
        // 	yearTwo:date.getFullYear().substr(1,2);
        // 	yearThree:date.getFullYear().substr(2,3);
        // 	yearFour:date.getFullYear().substr(3,4);
        // 	mouthOne:month.substr(0,1);
        // 	mouthTwo:month.substr(1,2);
        // 	dateOne:strDate.substr(0,1);
        // 	dateTwo:strDate.substr(1,2);
        // 	hourOne:Hours.substr(0,1);
        // 	hourTwo:Hours.substr(1,2);
        // 	minuteOne:Minutes.substr(0,1);
        // 	minuteTwo:Minutes.substr(1,2);
        // 	secondOne:Seconds.substr(0,1);
        // 	secondTwo:Seconds.substr(1,2);
        // }
        // return newobj;
      },
      getBefore(transformNum) {
        // return 'transform:translateY(-'+transformNum*32+'px)'
        return 'step' + transformNum;
      }
    }
  }
</script>

<style>
  #timer {
    height: 1.04rem;
    width: 100%;
    background-color: #cb9dff;
    border-radius: 0.6rem;
    box-sizing: border-box;
    padding: 0.05rem;
    color: #fffae2;
    font-size: 0.3rem;
  }

  /* .flip-list-move {
  	transition: transform 1s;
  } */

  .wheel {
    width: 15px;
    height: 52px;
    line-height: 52px;
    position: relative;
    margin-left: 3px;
    will-change: transform;
    display: inline-block;
    overflow: hidden;
    text-align: center;
    vertical-align: middle;
  }

  .wheel::before {
    content: "0 \D\A 1 \D\A 2 \D\A 3 \D\A 4 \D\A 5 \D\A 6 \D\A 7 \D\A 8 \D\A 9 \D\A";
    position: absolute;
    display: block;
    width: 100%;
    transition: all 1s ease-in-out;
    padding-left: 3px;
  }

  .step0::before {
    transform: translateY(0px);
  }

  .step1::before {
    transform: translateY(-52px);
  }

  .step2::before {
    transform: translateY(-104px);
  }

  .step3::before {
    transform: translateY(-156px);
  }

  .step4::before {
    transform: translateY(-208px);
  }

  .step5::before {
    transform: translateY(-260px);
  }

  .step6::before {
    transform: translateY(-312px);
  }

  .step7::before {
    transform: translateY(-364px);
  }

  .step8::before {
    transform: translateY(-416px);
  }

  .step9::before {
    transform: translateY(-468px);
  }
</style>



