<template>
  <div id="pcIndex">
    <div class="logdiv">
      <!-- <div class="logtitle">Login ...</div> -->
      <input type="password" placeholder="Login ..." class="loginput" v-model="pwd" @keyup="login"/>
    </div>
  </div>
</template>

<script>
  export default{
    name:'pcIndex',
    data(){
      return{
        pwd:''
      }
    },
    methods:{
      login(){
        if(this.pwd=='babyily'){
          this.$router.push('dataList')
        }
      }
    }
  }
</script>

<style scoped="scoped" lang="scss">
  #pcIndex{
    height: inherit;
    background-color: #e2e9ff;
    position: relative;
  }
  .logdiv{
    position: absolute;
    left: 50%;
    top: 38%;
    transform: translate(-50%,-50%);
  }
  .logtitle{
    margin-bottom: 2rem;
    text-align: center;
    font-size: 3rem;
    color: #0d1e7f;
  }
  .loginput{
    width: 36vw;
    height: 4rem;
    border-radius: 0.3rem;
    border: 1px solid #cb9dff;
    outline: none;
    font-size: 2rem;
    box-sizing: border-box;
    padding: 0 2rem;
    box-shadow: 0px 0px 3px 1px #cb9dff;
    color: #0d1e7f;
  }
  ::-webkit-input-placeholder { /* WebKit browsers */
    color: #999;
  }
</style>



