import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import mobileIndex from '@/components/mobileFiles/mobileIndex'
import homeList from '@/components/mobileFiles/homeList/homeList'
import dataList from '@/components/mobileFiles/homeList/dataList'
import storyListDetail from '@/components/mobileFiles/storyListDetail/storyListDetail'
import storylist from '@/components/pcFiles/storylist/storylist'
import pcIndex from '@/components/pcFiles/pcIndex'
Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'home',
      component: home
    },
    // {
    //   path: '/mobileIndex',
    //   name: 'mobileIndex',
    //   component: mobileIndex
    // },
    // 移动端******************************************************************
    {
      path: '/homeList',
      name: 'homeList',
      component: homeList
    },
    {
      path: '/storyListDetail',
      name: 'storyListDetail',
      component: storyListDetail
    },
    // pc端********************************************************************
    {
      path: '/dataList',
      name: 'dataList',
      component: dataList,
      children:[
        {
          path: '/storylist',
          name: 'storylist',
          component: storylist
        },
      ]
    }
  ]
})



